<template>
<div>

  <el-carousel arrow="always" :height="BookStyle.height" indicator-position='none' :autoplay="false">
    <el-carousel-item v-for="item in 5" :key="item">
       <el-row>

        <el-col :span="12">
          <div class="book-left" :style="LeftStyle">
            <div>{{item}} Left</div>
          </div>
        </el-col>

        <el-col :span="12">
          <div class="book-right" :style="RightStyle">
            <div>{{item}} Right</div>
          </div>
        </el-col>

      </el-row>
    </el-carousel-item>
  </el-carousel>

</div>
</template>

<script>
import bus from '../../components/bus.js';

const headerHeight = 50;

export default {
  data() {
    return {
      BookStyle: {
        height: window.innerHeight - headerHeight + 'px',
      },
      LeftStyle: {
        height: window.innerHeight - headerHeight + 'px',
        'background-color': '#4081cc',
        width: (window.innerWidth+0.5) / 2 + 'px',
      },
      RightStyle: {
        height: window.innerHeight - headerHeight + 'px',
        'background-color': '#204291',
        width: (window.innerWidth+0.5) / 2 + 'px',
      },
    }
  }, // data
  created() {
    bus.$on('communityResize', win => {
      let height = win.innerHeight - 50 + 'px';
      this.LeftStyle.height = height;
      this.RightStyle.height = height;
      this.BookStyle.height = height;
    })
  },
}
</script>

<style scoped>
.el-row {
  margin: 0;
  padding: 0;
  margin-bottom: 0px;
}
.el-col {
  border-radius: 20px;
}
.el-carousel {
  z-index: 0;
}

.book-left, .book-right{
  /* display: table-cell; */
  /* vertical-align: middle; */
  border-radius: 15px;
  color: white;
  width: 100%;
  margin: 0;
}
</style>